.tab-container {
  height: 4.11vw;
  padding: 0 2.5vw;
}
.tab-container .tab-item {
  cursor: pointer;
  font-size: 0.94vw;
  font-weight: normal;
  font-stretch: normal;
  color: #888888;
}
.tab-container .tab-item.active {
  color: #555555;
}
.banner-container {
  padding: 0 2.5vw;
}
.banner-container .scale {
  padding-bottom: 77.3%;
  /*相当于盒子的高度*/
  width: 25%;
  /*即维持宽高比为1:2*/
  height: 0;
  position: relative;
}
.banner-container .scale > div {
  position: absolute;
  width: 100%;
  height: 100%;
}
.banner-container .banner-item {
  width: 100%;
  margin-bottom: 0;
  background-color: #304ba3;
}
.banner-container .banner-item .banner-info {
  height: 16.85vw;
  width: 100%;
  top: 0;
  color: #bbbbbb;
  padding: 0;
}
.banner-container .banner-item .banner-info .banner-t1 {
  position: absolute;
  left: 1.25vw;
  top: 1.82vw;
  font-size: 1.56vw;
  letter-spacing: 0vw;
}
.banner-container .banner-item .banner-info .banner-t2 {
  position: absolute;
  left: 1.25vw;
  top: 21.3vw;
  padding-top: 0vw;
  font-size: 0.94vw;
}
.page-nav {
  background-color: #fff;
  padding: 2.03vw 0 4.58vw;
}
.page-nav .more {
  display: block;
  margin: 0 auto;
  width: 13.02vw;
  height: 3.13vw;
  border-radius: 0.05vw;
  background-color: #dddddd;
  border-radius: 1px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
.page-nav .more:hover {
  background-image: url(data:image/png;base64,);
}
.nav {
  padding: 2.81vw 0;
}
.text3 {
  margin: 0 2.97vw;
  width: 17.19vw;
  height: 3.13vw;
  line-height: 3.13vw;
  text-align: center;
  background-color: #eeeeee;
  border-radius: 1px;
  font-family: SourceHanSansCN-Light;
  font-size: 1.56vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #555555;
}
.visiable-b {
  display: flex;
}
.visiable-small {
  display: none;
}
@media (max-width: 768px) {
  .case-swiper-container {
    padding: 3.98vw 0;
  }
  .swiper-slide {
    width: 14.91vw;
    height: 2.78vw;
    font-size: 2.78vw;
    line-height: 2.78vw;
    color: #888888;
    text-align: center;
  }
  .swiper-slide:nth-child(1) {
    width: 9% !important;
  }
  .swiper-slide:nth-child(2) {
    width: 17% !important;
  }
  .swiper-slide:nth-child(3) {
    width: 18% !important;
  }
  .swiper-slide:nth-child(4) {
    width: 23% !important;
  }
  .swiper-slide:nth-child(5) {
    width: 14% !important;
  }
  .swiper-slide:nth-child(5) {
    width: 17% !important;
  }
  .carousel {
    height: 670px;
    background-color: #1b358a;
  }
  .tab-container {
    height: 10.6vw;
  }
  .tab-container .tab-item {
    cursor: pointer;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 30px;
    letter-spacing: 0px;
    color: #888888;
  }
  .tab-container .tab-item.active {
    color: #555555;
  }
  .banner-container {
    position: relative;
    padding: 0 0;
    margin: 0px;
  }
  .banner-container .scale {
    padding-bottom: 77.3%;
    /*相当于盒子的高度*/
    width: 25%;
    /*即维持宽高比为1:2*/
    height: 0;
    position: relative;
  }
  .banner-container .scale > div {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .banner-container .scale > div .img {
    width: 100%;
    height: 100%;
  }
  .banner-container .banner-item {
    width: 100%;
    margin-bottom: 17.78vw;
    background-color: #304ba3;
  }
  .banner-container .banner-item .banner-info {
    height: 16.85vw;
    width: 100%;
    top: 100%;
    color: #bbbbbb;
    background-color: #eaeaea;
  }
  .banner-container .banner-item .banner-info .banner-t1 {
    left: 4.72vw;
    top: 4.17vw;
    height: 3.15vw;
    font-size: 3.33vw;
    letter-spacing: 0vw;
  }
  .banner-container .banner-item .banner-info .banner-t2 {
    left: 4.72vw;
    top: 10.37vw;
    height: 2.5vw;
    font-size: 2.22vw;
  }
  .banner-container .text-1 {
    font-family: SourceHanSansCN-Regular;
    font-size: 30px;
    color: #bbbbbb;
    position: absolute;
    left: 24px;
    top: 35px;
  }
  .banner-container .text-2 {
    font-family: SourceHanSansCN-Light;
    font-size: 18px;
    color: #bbbbbb;
    position: absolute;
    bottom: 42px;
    left: 24px;
  }
  .page-nav {
    height: 24.35vw;
    background-color: #fff;
    padding: 6.48vw 0 12.31vw;
  }
  .page-nav .more {
    width: 23.15vw;
    height: 5.56vw;
    border-radius: 0.09vw;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 150%;
  }
  .page-nav .more:hover {
    background-image: url(data:image/png;base64,);
  }
  .dash-line {
    margin: 0 4.81vw;
  }
  .nav {
    padding: 9.17vw 0 8.43vw;
  }
  .text3 {
    width: 35.93vw;
    height: 7.59vw;
    line-height: 7.59vw;
    border-radius: 0.09vw;
    text-align: center;
    font-size: 3.33vw;
  }
  .visiable-b {
    display: none;
  }
  .visiable-small {
    display: flex;
  }
}
